

.main-header {
  display: flex;
  margin-top: 15px;
  justify-content: space-between;
  background-color: $white;
  color: $black;
}

.main-header__wrapper {
  padding-left: 20px;
  padding-right: 20px;
  justify-content: space-between;
  width: 100%;
}

.main-header__logo {
  display: flex;
  height: 65px;
  text-align: center;
}

.main-header__logo-title {
  margin-left: 20px;
}

.main-header__logo-picture__tablet {
  display: none;
}

.main-nav {
  position: relative;
  min-height: 200px;
  background-color: transparent;
}


.main-nav__toggle {
  position: absolute;
  top: 6px;
  right: 1px;
  z-index: 2;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: transparent;
  color: $black;
}

.main-header__top {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 52px;
}

.main-nav--closed {
  display: none;
}

.main-nav__toggle .main-nav__toggle--closed::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: $black;
  box-shadow: 0 8px 0 0 $black, 0 16px 0 0 $black;
}

.main-nav__list-link {
  display: inline-block;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  color: $black;
  padding-top: 22px;
  padding-bottom: 23px;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
}

.main-nav__list-item {
  margin-left: -20px;
  margin-right: -20px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}

.main-nav__list-item--active .main-nav__list-link {
  position: relative;
}

.main-nav__list-item--active .main-nav__list-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 14px;
  width: 100%;
  height: 2px;
  background-color: $green;
}


@media (min-width: $tablet-width) {
  .main-header {
    display: flex;
    margin: 0 auto;
    height: 50px;
    margin-top: 25px;
  }

  .main-header__wrapper {
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .main-nav__list-item {
    border: none;
    padding: 10px 36px;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
  }

  .main-header__logo-picture {
    width: 60px;
    height: 50px;
  }

  .main-nav__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-left: 9px;
  }

  .main-header__logo-picture__tablet {
    display: block;
  }


  .main-nav {
    min-height: 50px;
    width: 66%;
  }

  .main-header__top {
    justify-content: flex-start;
    width: 27.11%;
    padding: 0;
  }

  .main-nav__toggle {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .main-nav__list {
    justify-content: flex-end;
    width: 100%;
    margin-left: 11px;
  }

  .main-header__wrapper {
    padding-left: 110px;
    padding-right: 110px;
  }

  .main-header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 31px;
    background-color: transparent;
  }

  .main-nav__list-link {
    color: $white;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    padding-top: 31px;
  }

  .main-nav__list-item--active .main-nav__list-link::after {
    color: $white;
  }

}
